.banner {
  background: url('../images/bg_home_mobile.jpg')  center center no-repeat;
  background-size: cover;
  width: 100%;
  height: 336px;

  @include desktop {
    background: url('../images/bg_home.jpg');
    background-size: contain;
    height: 680px;
  }

  .square {
    width: 10px;
    height: 10px;
    background-color: #fff;
  }

  h2 {
    font-size: 24px;
    line-height: 35px;
    @include desktop {
      font-size: 48px;
      line-height: 1.5;
    }
  }

  p {
    font-size: 14px;
    line-height: 20px;
    font-weight: 300;

    @include desktop {
      font-size: 18px;
      line-height: 26px;
    }
  }
}

.course {
  h2 {
    font-size: 24px;
    line-height: 35px;
  }

  .card {
    // background-color: $bac;
    &:hover {
      color: #ffffff;
      background: $black-500;
      border: 1px solid $primary;
      cursor: pointer;
      transition: 0.5s;

      .material-icons {
        @include text-gradient;
      }
    }
  }
}

.sop {
  ul > li{
    &:nth-of-type(1){
      .card {
        @include after-number {
          content: '01';
        }
      }
    }
    &:nth-of-type(2){
      .card {
        @include after-number {
          content: '02';
        }
      }
    }
    &:nth-of-type(3){
      .card {
        @include after-number {
          content: '03';
        }
      }
    }
    &:nth-of-type(4){
      .card {
        @include after-number {
          content: '04';
        }
      }
    }
  }
}

.hot {
  .card {
    border-radius: 24px;

    &:hover {
      border: 1px solid $primary;
      transition: 0.5s;
    }

    figure {
      width: 100px;
      height: 100px;
    }

    h4 {
      font-size: 20px;
      line-height: 29px;
    }
  }

  .btnbtn {
    color: #ffffff;
    background: linear-gradient(104deg, #09ACF5, #62DB54);
    border: 1px solid transparent;

    &:hover {
      background: linear-gradient(104deg, #09ACF5, #62DB54);;
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
      border: 1px solid $primary;
    }
  }
}

.info {
  min-width: 100%;
  height: 615px;
}
.info-group > li {
  &:hover {
    color: #ffffff;
    .material-icons {
      color: $primary;
    }
    .after-divider-white {
      &:after {
        background-color: $primary;
      }
    }
    .shade-tertiary {
      background-color: rgba(28, 28, 28, 0.12);
    }
  }

  &:nth-of-type(1) {
    background:  url('../images/info01.jpg') center center / cover ;
  }
  &:nth-of-type(2) {
    background:  url('../images/info02.jpg') center center / cover ;
  }
  &:nth-of-type(3) {
    background:  url('../images/info03.jpg') center center / cover ;
  }
  &:nth-of-type(4) {
    background:  url('../images/info04.jpg') center center / cover ;
  }


}

.article {
  margin-top: 368px;
  @include desktop {
    margin-top: 0;
  }
  .squareRounded {
    display: none;
    width: 32px;
    height: 32px;
    &-topLeft {
      border-top: 2px solid $primary;
      border-left: 2px solid $primary;
      border-top-left-radius: 10px;
    }
    &-topRight {
      border-top: 2px solid $primary;
      border-right: 2px solid $primary;
      border-top-right-radius: 10px;
    }
    &-bottomLeft {
      border-bottom: 2px solid $primary;
      border-left: 2px solid $primary;
      border-bottom-left-radius: 10px;
    }
    &-bottomRight {
      border-bottom: 2px solid $primary;
      border-right: 2px solid $primary;
      border-bottom-right-radius: 10px;
    }
  }

  .card {
    background: $black-400;

    &:hover {
      .squareRounded {
        display: block;
      }
    }
  }
}

.feedback {
  background-image: url('../images/bg_share.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.feedback .swiper {
  width: 100%;
  height: 100%;
  @include desktop {
    height: 429px;
  }
  &-img {
    top: 0;
    left: 10px;
    width: calc(100% - 12px);
    height: 213px;
    left: 5px;
    margin-bottom: 40px;
    border-radius: 24px;
    position: relative;

    &::after {
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      border: 1px solid #7C7E77;
      border-radius: 24px;
      left: -10px;
      top: 10px;
      z-index: -1;
      @include desktop {
        left: -5px;
        top: 5px;
      }
    }

    img {
      object-fit: cover;
      border-radius: 24px;
    }

    @include desktop {
      // position: absolute;
      width: 525px;
      height: 381px;
    }
  }

  .card {
    position: relative;
    width: 100%;
    padding: 24px;
    margin-top: 320px;

    border-radius: 24px 24px 24px 0;

    &-img {
      position: absolute;
      width: 80px;
      height: 80px;
      right: 24px;
      top: -50px;
      bottom: auto;
      transform: none;
      @include desktop {
        width: 100px;
        height: 100px;
        right: 22px;
        top: -60%;
        transform: translateY(50%);
      }
    }
    @include desktop {
      position: absolute;
      width: 45%;
      padding: 32px;
      left: 50%;
      margin-top: 250px;
    }
  }

  .swiper-button-next:after, .swiper-button-prev:after {
    content: '';
  }

  &-button-next, &-button-prev {
    position: unset;
    width: 24px;
    height: 24px;
    margin-top: 0;
    color: $primary;
  }

  .swiper-pagination {
    position: unset;
    width: auto;
    .swiper-pagination-bullet {
      background-color: $primary;
      opacity: initial;
      &-active {
        width: 16px;
        border-radius: 100px;
      }
    }
  }

  &-buttons {
    position: initial;
    transform: none;
    bottom: 0;
    left: 30%;
    z-index: 10;
    transform: none;
    margin-top: 32px;
    @include desktop {
      position: absolute;
      transform: translateX(-50%);
      margin-top: 0;
    }
  }
}

.subscribe {
  .material-symbols-outlined {
    font-size: 80px;
  }
  a {
    &:hover {
      background-color: $primary;
    }
  }
}
