body {
  font-family: 'Azeret Mono', 'Noto Sans TC', monospace, sans-serif;
  color: #ffffff;
}

.font-mono {
  font-family: 'Azeret Mono', monospace;
}

.font-tc {
  font-family: 'Noto Sans TC', sans-serif;
}

.text-f-primary {
  font-size: 18px;
  line-height: 26px;
}

.text-gradient {
  background: linear-gradient($color-secondary, $color-primary);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

.fs-32 {
  font-size: 32px;
  line-height: 37px;
}

.text-lg-start {
  @include desktop {
    text-align: start;
  }
}
