// after
.after-divider-center {
  @include after-divider {
    left: 43%;
    @include desktop {
      left: 48%;
    }
  };
}

.after-divider-start {
  @include after-divider{
    top: 80%;
    left: 43%;

    @include desktop {
      left: 0%;
    }
  };
}

.after-divider-white {
  @include after-divider-white {
    @include desktop {
      left: 0%;
    }
  }
}

// shade
.shade-tertiary {
  background-color: rgba(28, 28, 28, 0.9);
}

// rounded
.rounded-24 {
  border-radius: 24px;
}

// icon
.icon-fill {
  font-variation-settings:
  'FILL' 1,
}

// margin
.mx-12px {
  margin: 0 12px;
}

// position
.isolate{
  isolation: isolate;
}
