// RWD
@mixin mobile {
  @media screen and(min-width: 576px) {
    @content
  }
};

@mixin desktop {
  @media screen and(min-width: 992px) {
    @content
  }
};

// font
@mixin text-gradient {
  background: linear-gradient($color-secondary, $color-primary);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
};

@mixin font-mono {
  font-family: 'Azeret Mono', monospace;
};

// after
@mixin after-number {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: -32px;
    left: 40px;
    color: $primary;
    font-size: 48px;
    line-height: 56px;
    font-weight: bold;
    @include font-mono;
    background: $black-400;
    padding: 4px;
    @content
  }
};

@mixin after-divider{
  position: relative;
  &:after {
    content: '';
    position: absolute;
    bottom: -16px;
    left: 48%;
    width: 50px;
    height: 3px;
    background-color: $color-primary;
    @content
  }
};

@mixin after-divider-white {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    bottom: -16px;
    left: 43%;
    width: 50px;
    height: 3px;
    background-color: #ffffff;
    @content
  }
}
