.courseInfo {
  ul.nav > li {
    &:hover {
      color: $primary;
      border: 1px solid $primary !important;
      transition: 0.5s;
    }
  }

  .card {
    border-radius: 24px;

    figure {
      width: 100px;
      height: 100px;
    }

    h4 {
      font-size: 20px;
      line-height: 29px;
      @include desktop {
        font-size: 24px;
        line-height: 35px;
      }
    }
  }

  .card-footer {
    width: 100%;
    a {
      width: 100%;
    }
    @include desktop {
      width: auto;
      a {
        // width: auto;
      }
    }
  }

  .btn-book {
    &:hover {
      background-color: $primary;
    }
  }

  .btnbtn {
    color: #ffffff;
    background: linear-gradient(104deg, #09ACF5, #62DB54);
    border: 1px solid transparent;

    &:hover {
      background: linear-gradient(104deg, #09ACF5, #62DB54);;
      background-clip: text;
      -webkit-background-clip: text;
      color: transparent;
      border: 1px solid $primary;
    }
  }
}
